/**
* -
* @module 
* @desc 
* @author 
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body
               width="750px">
        <div class="punish-block" v-if="form" v-loading="loading">
            <h3>{{form.pmName}}</h3>
            <div class="enforceCode"><span>{{form.pmNum}}</span></div>
            <el-divider content-position="left"><span class="divider-span">{{form.isEffective | YESORNO}}</span>
            </el-divider>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label></label></el-col>
                        <el-col :span="16">{{form.enforceName}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label></label></el-col>
                        <el-col :span="16">{{form.lawName}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">{{form.violationRegulations}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">{{form.accordingRegulations}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">{{form.violationRegulationsContent}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">{{form.accordingRegulationsContent}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">{{form.rightclause}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label></label></el-col>
                        <el-col :span="20">
                            <el-tag v-if="form.isWarning"></el-tag>
                            <el-tag v-if="form.isChange"></el-tag>
                            <el-tag v-if="form.isChangebefore"></el-tag>
                            <el-tag v-if="form.isFine"></el-tag>
                            <el-tag v-if="form.isRevokeGoods"></el-tag>
                            <el-tag v-if="form.isConfiscate"></el-tag>
                            <el-tag v-if="form.isStop"></el-tag>
                            <el-tag v-if="form.isRevoke"></el-tag>
                            <el-tag v-if="form.isForceDismantle"></el-tag>
                            <el-tag v-if="form.isDedention"></el-tag>
                            <el-tag v-if="form.isOtherPunish"></el-tag>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label></label></el-col>
                        <el-col :span="16">{{form.naturalRangeMin}}-{{form.naturalRangeMax}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label></label></el-col>
                        <el-col :span="16">{{form.legalRangeMin}}-{{form.legalRangeMax}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-divider content-position="right" v-if="form.effectiveTime">
                <span
                        class="divider-span">{{ form.effectiveTime | formatDate}}
                    <span v-if="form.invalidTime"> {{ form.invalidTime | formatDate}}</span>
                </span>
            </el-divider>
        </div>
    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../../until/wordBook";
    import {apiPunishMatterGet} from "../../../../api/case";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                loading:true,
                form: null,
                enforceList: [],
                onlyShow: false,
                see: false
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            //dataform
            initPage() {
                if (this.editId) {
                    apiPunishMatterGet(this.editId).then(res => {
                        this.loading=false;
                        this.form = res.data;
                    })
                }
            }
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .enforceCode {
        text-align: center;
        color: #aaaaaa;
        font-size:  @fontSize ;
        margin-top: 5px
    }

    .el-tag {
        margin: 0 3px;
    }

    .punish-block {
        h3 {
            text-align: center;
        }

        .divider-span {
            color: #cccccc;
        }

        .punish-row {
            line-height: 30px;

            label {
                display: inline-block;
                color: #8ca7b2;
                width: 120px;
                text-align: right;
            }
        }
    }

    .concss {
        font-weight: bold
    }
</style>
